.contactSection {
  display: block;
  position: relative;
  padding-bottom: 50px;
  padding-top: 10px;
}

.contactSection .lineSection {
  background-image: linear-gradient(to bottom, green, rgb(0, 0, 0));
}

.contactSection .contactContainer {
  margin-top: 20px;
}

.contactSection .contactRowSocial {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin-top: 40px;
}

.contactSection .contactBox {
  display: inline-grip;
  grid-template-columns: 40% 60%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: black;

  border-style: solid;
  width: 300px;
  transition: 0.5s;
  background-color: rgb(255, 255, 255);
  border-color: rgb(20, 130, 255);
  border-radius: 15px;
}

.contactSection .contactBox:hover {
  background-color: #42f587;
}

.contactSection a:link {
